.filter {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 43px;
    
  }
  
  .filter__text {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    margin-left: 10px;
  }
  
  .filter__checkbox {
    width: 36px;
    height: 20px;
    border: none;
    outline: none;
    background-color: #2f2f2f;
    box-shadow: 0px 4px 4px rgba(50, 50, 71, 0.08), 0px 4px 8px rgba(50, 50, 71, 0.06);
    border-radius: 16px;
    position: relative;
    cursor: pointer;
    appearance: none;
  }
  .filter__checkbox:checked{
    background: #3DDC84;
  }
  .filter__checkbox::after {
    height: 16px;
    width: 16px;
    position: absolute;
    top: 2px;
    left: 2px;
    background: #ffffff;
    content: '';
    border-radius: 50%;
    transition: 0.3s ease;
  }
  
  .filter__checkbox:checked::after {
    height: 16px;
    width: 16px;
    position: absolute;
    top: 2px;
    right: 2px;
    content: '';
    border-radius: 50%;
    transition: 0.4s ease;
    transform: translate(16px);
  }
  
  @media screen and (min-width:768px) and (max-width:1279px) {
    .filter{
      margin-top: 38px;
    }
  }

  @media screen and (min-width:320px) and (max-width:767px) {
    .filter{
      margin-top: 45px;
    }

    .filter__text{
      font-size: 11px;
    }
  }